.side-panel {
    position: absolute;
    top: 120px;
    right: 0; //height: 335px;
    background-color: #fff;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    padding: 15px;
    z-index: $zindex_side_panel;
    &__hamburger-bullet {
        position: absolute;
        width: 10px;
        height: 10px;
        background-color: #000;
        display: block;
        border-radius: 50%;
        left: 50%;
        transform: translateX(-50%);
        //transition: all .3s;
        &--start,
        &--end,
        &--center {
            transition: top .9s .3s, border-radius .6s .3s, width .6s .3s, height .6s .3s, transform .3s, opacity .9s, left .3s;
        }
        &--start {
            top: 0;
        }
        &--center {
            top: 15px;
        }
        &--end {
            top: 30px;
        }
        .side-panel__menu-link:hover & {
            background-color: $brand_color;
            //transition: background-color ease .3s;
        }
    }
    &__menu {
        &-list {
            padding-left: 0;
        }
        &-list-item {
            margin: 15px 0;
            text-align: center;
            &--hamburger {
                margin: 5px 15px;
                min-height: 45px;
                position: relative;
            }
            &--phone {
                margin-bottom: 0;
            }
            &.active {
                .side-panel__hamburger-bullet--start,
                .side-panel__hamburger-bullet--end {
                    transition: top .3s, border-radius .6s .3s, width .6s .3s, height .6s .3s, transform .9s .3s, opacity .3s, left .9s .3s;
                    top: 15px;
                    width: 40px;
                    border-radius: 0;
                    height: 2px;
                    left: -150%;
                    transform: rotate(45deg);
                }
                .side-panel__hamburger-bullet--center {
                    transition: all .3s;
                    opacity: 0;
                }
                .side-panel__hamburger-bullet--end {
                    transform: rotate(-45deg);
                }
            }
        }
        &-link {
            &:hover {
                transition: background-color ease .3s;
            }
            display: inline-block;
        }
        &-icon {
            display: block;
        }
    }
}